@charset "UTF-8";
:root {
  --flatify__max-font-size: 18;
  --flatify__min-font-size: 14;
  --flatify__max-font-size-with-unit: 18px;
  --flatify__min-font-size-with-unit: 14px;
  --flatify__primary-font-family: Varela Round, sans-serif;
  --flatify__heading-font-family: Varela Round, sans-serif;
  --flatify__code-font-family: SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace;
  --flatify__base-line-height: 1.5;
  --flatify__small-line-height: 1.2;
  --flatify__large-line-height: 1.8;
  --flatify__medium-font-weight: 500;
  --flatify__bold-font-weight: 700;
  --flatify__body-text-align: initial;
  --flatify__heading-font-style: normal;
  --flatify__heading-font-weight: bold;
  --flatify__heading-mb: 0.5em;
  --flatify__paragraph-mb: 2em;
  --flatify__link-decoration: none;
  --flatify__link-decoration__hover: underline 2px currentColor;
  --flatify__link-decoration__focus: none;
  --flatify__bg-color: #ffffff;
  --flatify__bg-color-dark: #f1f4f7;
  --flatify__bg-color-darker: #ced9e3;
  --flatify__txt-color: #2e4051;
  --flatify__txt-color-light: #77838e;
  --flatify__txt-color-dark: #292b36;
  --flatify__txt-color-inverted: #f1f4f7;
  --flatify__heading-color: currentColor;
  --flatify__blue: #1cb0f6;
  --flatify__blue-light: #6bccf9;
  --flatify__blue-dark: #1e94ce;
  --flatify__green: #58cc02;
  --flatify__green-light: #92de5b;
  --flatify__green-dark: #4eaa0b;
  --flatify__yellow: #ffde00;
  --flatify__yellow-light: #ffea59;
  --flatify__yellow-dark: #d4b909;
  --flatify__orange: #ff9600;
  --flatify__orange-light: #ffbb59;
  --flatify__orange-dark: #d47f09;
  --flatify__red: #ff4b4b;
  --flatify__red-light: #ff8a8a;
  --flatify__red-dark: #d44345;
  --flatify__purple: #ce82ff;
  --flatify__purple-light: #dfaeff;
  --flatify__purple-dark: #ad6fd5;
  --flatify__accent-color: #1cb0f6;
  --flatify__accent-color-light: #6bccf9;
  --flatify__accent-color-dark: #1e94ce;
  --flatify__tap-highlight-color: transparent;
  --flatify__link-color: #1cb0f6;
  --flatify__link-color__hover: #6bccf9;
  --flatify__link-color__focus: #1e94ce;
  --flatify__simple-transition: 0.18s ease-in-out;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@media (max-width: 575.98px) {
  :root {
    font-size: var(--flatify__min-font-size-with-unit);
  }
}
@media (min-width: 576px) and (max-width: 1200px) {
  :root {
    font-size: calc( var(--flatify__min-font-size-with-unit) + ( var(--flatify__max-font-size) - var(--flatify__min-font-size) ) * ( ( 100vw - 575px ) / ( 1200 - 575 ) ) );
  }
}
@media (min-width: 1200px) {
  :root {
    font-size: var(--flatify__max-font-size-with-unit);
  }
}
@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}

body {
  margin: 0;
  font-family: var(--flatify__primary-font-family);
  line-height: var(--flatify__base-line-height);
  text-align: var(--flatify__body-text-align);
  color: var(--flatify__txt-color);
  background-color: var(--flatify__bg-color);
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: var(--flatify__tap-highlight-color);
  text-rendering: optimizeLegibility;
}

hr {
  margin: 1em 0;
  opacity: 0.5;
  color: currentColor;
  background-color: currentColor;
  border: none;
}
hr:not([size]) {
  height: 0.18em;
}
hr.dots {
  line-height: 1;
  display: block;
  height: auto;
  padding: 2em;
  text-align: center;
  background: transparent;
}
hr.dots:before {
  content: "‌•••";
  letter-spacing: 1.4em;
  font-size: 1.4em;
  font-weight: bold;
}
hr.short {
  width: 9em;
  margin: 3em auto;
}

h6, h5, h4, h3, h2, h1 {
  margin-top: 0;
  margin-bottom: var(--flatify__heading-mb);
  font-family: var(--flatify__heading-font-family, --flatify__primary-font-family);
  font-style: var(--flatify__heading-font-style);
  font-weight: var(--flatify__heading-font-weight);
  color: var(--flatify__heading-color);
}

h1 {
  font-size: 3.5em;
  line-height: var(--flatify__small-line-height);
}

h2 {
  font-size: 3em;
  line-height: var(--flatify__small-line-height);
}

h3 {
  font-size: 2.35em;
  line-height: var(--flatify__small-line-height);
}

h4 {
  font-size: 2em;
  line-height: var(--flatify__base-line-height);
}

h5 {
  font-size: 1.65em;
  line-height: var(--flatify__base-line-height);
}

h6 {
  font-size: 1.35em;
  line-height: var(--flatify__base-line-height);
}

p {
  line-height: var(--flatify__large-line-height);
  margin-top: 0;
  margin-bottom: var(--flatify__paragraph-mb);
}
p:last-of-type {
  margin-bottom: 0;
}

abbr[title] {
  cursor: help;
  text-decoration: var(--flatify__txt-color-light) underline dotted 0.125em;
  text-decoration-skip-ink: none;
}

address {
  margin-bottom: 1em;
  font-style: normal;
  line-height: inherit;
}

ol,
ul {
  padding-left: 2em;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1em;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: var(--flatify__medium-font-weight);
}

dd {
  margin-bottom: 0.5em;
  margin-left: 0;
}

blockquote {
  line-height: var(--flatify__large-line-height);
  margin: 0 0 var(--flatify__paragraph-mb);
  border-radius: 0.25em;
  border-style: solid;
  border-color: var(--flatify__accent-color);
  border-width: 0 0 0 0.5em;
}
blockquote:not(.large) {
  font-size: 1em;
  line-height: var(--flatify__large-line-height);
  padding: 0.75em 1.25em;
}
blockquote.large {
  font-size: 1.25em;
  border-width: 0.5em 0;
  padding: 1.25em 0.75em;
}
blockquote.large cite {
  padding-top: 1.25em;
}
blockquote.large:not(.text-align-fill) {
  text-align: center;
}
blockquote cite {
  display: block;
  font-size: 0.64em;
  font-style: normal;
  letter-spacing: 0.18em;
  text-transform: uppercase;
  padding-top: 0.5em;
  opacity: 0.8;
}

b,
strong {
  font-weight: var(--flatify__bold-font-weight);
}

mark {
  color: var(--flatify__text-color-darker);
}
mark:not(.inline) {
  border-radius: 0.38em;
  padding: 0 0.32em;
  background: var(--flatify__yellow-light);
}
mark.inline {
  position: relative;
  white-space: nowrap;
  margin: 0 0.32em;
  background: transparent;
}
mark.inline:before {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: var(--flatify__yellow-light);
  border-radius: 0.38em;
  opacity: 0.9;
  transform: rotate(-3deg) scale(1.05, 0.9) skew(-10deg);
}

sub,
sup {
  position: relative;
  font-size: 0.75em;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

a[href] {
  color: var(--flatify__link-color);
  text-decoration: var(--flatify__link-decoration);
  transition: all var(--flatify__simple-transition);
}
a[href]:hover {
  color: var(--flatify__link-color__hover);
  text-decoration: var(--flatify__link-decoration__hover);
}
a[href]:focus {
  color: var(--flatify__link-color__focus);
  text-decoration: var(--flatify__link-decoration__focus);
}

pre,
code,
kbd,
samp {
  direction: ltr;
  unicode-bidi: bidi-override;
  font-size: 1em;
  font-family: var(--flatify__code-font-family);
}

pre {
  overflow: auto;
  display: block;
  margin-top: 0;
  margin-bottom: 1em;
}
pre code {
  color: inherit;
  word-break: normal;
}

code {
  font-size: 1em;
  word-wrap: break-word;
}
a > code {
  color: inherit;
}

kbd {
  font-size: 0.9em;
  font-weight: normal;
  line-height: 1;
  display: inline-block;
  padding: 0.18em 0.25em 0.32em;
  margin: 0 0.25em;
  background: var(--flatify__bg-color-dark);
  border: 0.18em solid var(--flatify__bg-color-darker);
  border-radius: 0.38em;
  box-shadow: inset 0 -0.125em 0 var(--flatify__bg-color-darker);
  will-change: transform;
  transition: all var(--flatify__simple-transition);
}
@media (prefers-reduced-motion: no-preference) {
  kbd:hover {
    box-shadow: inset 0 0 0 var(--flatify__bg-color-darker);
    transform: translateY(0.15em);
  }
}
kbd kbd {
  padding: 0;
  background: transparent;
  border: none;
  box-shadow: none;
  transform: none !important;
}

figure {
  margin: 0 0 1em;
}

img,
svg {
  vertical-align: middle;
}

table {
  display: table;
  table-layout: fixed;
  caption-side: bottom;
  width: 100%;
}
table td,
table th {
  padding: 0.64em 1em;
}
table thead td,
table thead th {
  background: var(--flatify__bg-color-dark);
}
table:not(.bordered) {
  border-collapse: collapse;
}
table.bordered:not(.horizontal) {
  border-spacing: 0;
}
table.bordered:not(.horizontal) tr:first-child th,
table.bordered:not(.horizontal) tr:first-child td {
  border-top: 0.18em solid var(--flatify__bg-color-darker);
}
table.bordered:not(.horizontal) td,
table.bordered:not(.horizontal) th {
  border-left: 0.18em solid var(--flatify__bg-color-darker);
}
table.bordered:not(.horizontal) td:last-child,
table.bordered:not(.horizontal) th:last-child {
  border-right: 0.18em solid var(--flatify__bg-color-darker);
}
table.bordered:not(.horizontal) td,
table.bordered:not(.horizontal) th {
  border-bottom: 0.18em solid var(--flatify__bg-color-darker);
}
table.bordered:not(.horizontal) thead + tbody tr td,
table.bordered:not(.horizontal) thead + tbody tr th {
  border-top: none !important;
}
table.horizontal tr {
  flex-basis: 100%;
  white-space: nowrap;
}
table.horizontal thead {
  float: left;
}
table.horizontal thead td,
table.horizontal thead th {
  display: block;
}
table.horizontal tbody {
  display: flex;
  overflow-x: auto;
}
table.horizontal tbody tr {
  display: inline-block;
}
table.horizontal tbody td,
table.horizontal tbody th {
  display: block;
}
table.horizontal.bordered {
  border-spacing: 0;
}
table.horizontal.bordered thead tr {
  display: block;
  border-right: 0.18em solid var(--flatify__bg-color-darker);
}
table.horizontal.bordered thead td,
table.horizontal.bordered thead th {
  border-left: 0.18em solid var(--flatify__bg-color-darker);
}
table.horizontal.bordered tbody th,
table.horizontal.bordered tbody td {
  border-right: 0.18em solid var(--flatify__bg-color-darker);
}
table.horizontal.bordered td,
table.horizontal.bordered th {
  border-top: 0.18em solid var(--flatify__bg-color-darker);
}
table.horizontal.bordered td:last-child,
table.horizontal.bordered th:last-child {
  border-bottom: 0.18em solid var(--flatify__bg-color-darker);
}
table.horizontal thead tr th:first-child,
table.horizontal thead tr td:first-child {
  border-top-left-radius: 1em;
}
table.horizontal thead tr th:last-child,
table.horizontal thead tr td:last-child {
  border-bottom-left-radius: 1em;
}
table.horizontal tbody tr:last-child th:first-child,
table.horizontal tbody tr:last-child td:first-child {
  border-top-right-radius: 1em;
}
table.horizontal tbody tr:last-child th:last-child,
table.horizontal tbody tr:last-child td:last-child {
  border-bottom-right-radius: 1em;
}
table:not(.horizontal) tr:last-child th:first-child {
  border-top-left-radius: 1em;
}
table:not(.horizontal) tr:last-child th:last-child {
  border-top-right-radius: 1em;
}
table:not(.horizontal) tr:last-child td:first-child {
  border-bottom-left-radius: 1em;
}
table:not(.horizontal) tr:last-child td:last-child {
  border-bottom-right-radius: 1em;
}

caption {
  font-size: 0.9em;
  padding: 0.75em 0;
  opacity: 0.8;
}

th {
  font-weight: var(--flatify__medium-font-weight);
  text-align: inherit;
  text-align: -webkit-match-parent;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-center-x {
  display: flex;
  justify-content: center;
}

.flex-center-y {
  display: flex;
  align-items: center;
}

.flex-column-center-x {
  display: flex;
  align-items: center;
}

.flex-column-center-y {
  display: flex;
  justify-content: center;
}

.text-align-fill {
  hyphens: auto;
  text-align: justify;
}

.centered {
  text-align: center;
}